<template>
    <div class="cart">
        <h2></h2>
        <p v-show="!products.length"><i> </i></p>
        <ul>
            <li
                    v-for="product in products"
                    :key="product.id">
                {{ product.title }} - {{ product.price | currency }} x <input type="number" min="1" v-model="product.quantity"
                                                                              @input="updateQuantity({id: product.id, quantity: product.quantity})">
                <button type="button" @click="removeProductFromCart({id: product.id})"></button>
            </li>
        </ul>
        <p>: {{ total | currency }}</p>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        computed: {
            //       
            ...mapGetters('cart', {
                products: 'cartProducts',
                total: 'cartTotalPrice'
            })
        },
        //    
        methods: mapActions('cart', [
            'updateQuantity',
            'removeProductFromCart'
        ])
    }
</script>



